/* Scroll Container */
.scroll-container {
    display: flex;
    overflow-x: auto; /* Allow horizontal scrolling */
    overflow-y: hidden; /* Hide vertical scroll */
    height: 100vh; /* Full height */
    white-space: nowrap;
    scroll-snap-type: x mandatory; /* Snap sections for better experience */
    scroll-behavior: smooth; /* Smooth horizontal scrolling */
  }
  
  /* Hide default scrollbar and style custom one */
  .scroll-container::-webkit-scrollbar {
    height: 10px; /* Horizontal scrollbar height */
  }
  
  .scroll-container::-webkit-scrollbar-thumb {
    background-color: #888; /* Scrollbar thumb color */
    border-radius: 10px; /* Rounded edges */
  }
  
  .scroll-container::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Darker color on hover */
  }
  
  .scroll-container::-webkit-scrollbar-track {
    background-color: #f0f0f0; /* Scrollbar track background */
  }
  
  .section {
    min-width: 100vw; 
    height: 100vh; 
    scroll-snap-align: start; 
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
  }
  
  .section:nth-child(odd) {
    background-color: #ececec;
  }
  
  .scroll-container {
    display: flex;
    overflow-x: scroll;
    scroll-behavior: smooth;
  }
  
  .bottom-nav {
    position: fixed;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    display: flex;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.8); 
    border-radius: 30px;
    padding: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 40px;
  }
  
  .bottom-nav ul {
    list-style-type: none;
    display: flex;
    gap: 25px;
    margin: 0;
    padding: 0;
  }
  
  .bottom-nav li {
    cursor: pointer;
    font-size: 12px;
    color: #7a797a;
    transition: color 0.3s ease;
  }
  
  .bottom-nav li:hover {
    color: #7a38c8; /* Change to your preferred hover color */
  }
  .css-uks7c6-MuiSvgIcon-root {
    font-size: 18px !important;
  }
  
  .image-list-container {
    overflow: hidden; /* Hide overflow for the scroll effect */
    height: 650px; /* Set a fixed height for the scroll area */
  }
  
  .projects-scroll-container {
    display: flex;
    flex-direction: column; /* Align items vertically */
    animation: scroll 10s linear infinite; /* Adjust duration as needed */
    animation-play-state: running; /* Default to running */
  }
  
  .projects-scroll-container:hover {
    animation-play-state: paused; /* Pause on hover */
  }
  
  @keyframes scroll {
    0% {
      transform: translateY(0); /* Start position */
    }
    100% {
      transform: translateY(-50%); /* Scroll up by half the container height */
    }
  }
